.lead {
  background: #fff none repeat scroll 0 0;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
  margin-bottom: 50px;
  padding: 37px 20px 0;
  position: relative;
  z-index: 1;
}

.boxed .lead {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

.lead div {
  margin: 0 auto;
  max-width: 1110px;
  min-height: 296px;
  padding-bottom: 0;
  position: relative;
}

.lead div.clear {
  min-height: 0;
  padding: 0;
}

.lead h1 {
  font-size: 48px;
  font-weight: 300;
  line-height: 58px;
  padding: 17px 350px 0 0;
}

.lead p {
  color: #999999;
  font-size: 18px;
  line-height: 28px;
  margin-bottom: 0;
  padding-bottom: 59px;
  padding-right: 350px;
}

.lead p:last-child {
  margin-bottom: 0;
}

.lead img {
  bottom: 0;
  margin-bottom: 0 !important;
  position: absolute;
  right: 0;
}

@media (max-width: 1100px) {
  .lead div {
    min-height: 450px;
  }
}

.box-dark {
  background: #333333 none repeat scroll 0 0;
  color: #eeeeee;
  margin-bottom: 0;
  padding: 49px 0 41px;
}

.box-dark h1, .box-dark h2, .box-dark h3, .box-dark h4, .box-dark h5, .box-dark h6 {
  color: #ffffff;
}

.box-dark p:last-child {
  margin-bottom: 0;
}

.box-transparent {
  padding-bottom: 41px;
  padding-top: 11px;
}

.content .columns:first-child {
  margin-top: 50px;
}

.columns > article p:last-child {
  margin-bottom: 0;
}

.columns > h2:first-child, .why-us h2 {
  border-bottom: 1px solid #dfdfdf;
  font-size: 1.846em;
  font-weight: 300;
  margin: 0 0 1.5em;
}

.columns > h2:first-child {
